@import "../../styles/variables.scss";

.Logout {
  min-width: 320px;
  max-width: 800px;
  width: 100%;
  height: 2.5rem;
  font-size: 0.8rem;
  background-color: #000000;
  color: #ffffff;
  border: none;

  &:hover {
    cursor: pointer;
    color: #fd442c;
  }
}
.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 320px;
  max-width: 800px;
  width: 100%;
  height: 100%;
  flex: 1;

  .dashboard {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 320px;
    max-width: 800px;
    width: 100%;
    height: 65%;
    margin-bottom: 2rem;

    a {
      text-decoration: none;
    }

    .userInfo {
      display: flex;
      justify-content: space-around;
      padding: 1rem;
      min-width: 320px;
      max-width: 800px;
      width: 100%;
      color: #ffffff;

      img {
        width: 100px;
      }
    }
    .parties {
      color: #ffffff;
      margin: 1rem 0;
    }
    .partiesSlider {
      width: 100%;
      height: 300px;
      display: flex;
      overflow-y: hidden;
    }
    .partyIcon {
      width: 80%;
      height: 150px;
      margin: 0 1rem;
      padding: 0.5rem;
      border: 1px solid #ffffff;
      border-radius: 5px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      justify-content: center;
      background-color: $darkblue;

      &:hover {
        border: 1px solid $primary;
      }

      img {
        width: 150px;
        height: auto;
        border-radius: 5px;
        margin-bottom: 0.8rem;
      }

      h5 {
        color: #ffffff;
      }
    }
  }
  .newSparty {
    display: flex;
    justify-content: center;
    align-items: center;

    button {
      background-image: linear-gradient(to bottom right, $primary, #fd442c);
      padding: 2rem;
      border: 1px solid $secundary;
      border-radius: 5px;
      font-size: 2rem;
      color: #ffffff;
      animation-name: create;
      animation-duration: 3s;
      animation-iteration-count: infinite;

      &:hover {
        cursor: pointer;
        border: 1px solid #ffffff;
      }
    }
    @keyframes create {
      0% {
        background-image: linear-gradient(to bottom right, $primary, #fd442c);
      }
      50% {
        background-image: linear-gradient(to bottom right, #fd442c, $primary);
      }
      100% {
        background-image: linear-gradient(to bottom right, $primary, #fd442c);
      }
    }
  }
}
